.header {
  background: #3c3b3b;
  text-align: center;
  color: white;
  position: absolute;
  width: 100%;
  top: 3vh;
  font-family: radio;
  opacity: 0.8;
  font-family: radio, sans-serif;
}
@font-face {
  font-family: radio;
  src: url(/stevo-radio/static/media/Radio.844d259b.ttf);
}

.audio-player-container {
  position: absolute;
  top: 33vh;
  width: 100%;
  display: flex;
  justify-content: center;
}

.audio-player {
  display: flex;
  opacity: 0.5;
}

.footer {
  bottom: 1vh;
  position: absolute;
  width: 100%;
  text-align: center;
  background: #3c3b3b;
  color: white;
  font-family: radio;
  opacity: 0.9;
}

* {
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
  background-image: url(/stevo-radio/static/media/stevo.6d29878f.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
}

